import { registerBlockType } from '@wordpress/blocks';
import { __ } from '@wordpress/i18n';

import servicePattern from '../img/service-pattern.png';

import Edit from './edit';

// Icons
import icons from '../Const/icons';

// Attributes
const attributes = {
    cId: { type: 'string', default: '' },

    background: { type: 'object', default: { color: '#0000' } },
    hoverBG: { type: 'object', default: { type: 'image', image: { url: servicePattern }, overlayColor: '#0000', position: 'bottom left', size: 'auto' } },

    isIcon: { type: 'boolean', default: true },
    isUpIcon: { type: 'boolean', default: false },
    icon: { type: 'object', default: { class: 'fa fa-wordpress', fontSize: 70 } },
    upIcon: { type: 'object', default: { id: null, url: '', alt: '', title: '' } },
    iconWidth: { type: 'string', default: '70px' },
    iconBorder: { type: 'object', default: {} },

    isTitle: { type: 'boolean', default: true },
    title: { type: 'string', selector: '.ssbService h3.title', default: 'Service title' },
    titleColor: { type: 'string', default: '#222' },

    isDesc: { type: 'boolean', default: true },
    desc: { type: 'string', selector: '.ssbService h3.description', default: 'Lorem ipsum dolor sit, amet consectetur adipisicing elit. Accusamus, fuga.' },
    descColor: { type: 'string', default: '#333' },

    isLink: { type: 'boolean', default: true },
    link: { type: 'string', default: '#' },
    linkIn: { type: 'string', default: 'button' },
    linkIconColor: { type: 'string', default: '#fff' },
    linkBGColor: { type: 'string', default: '#d2d2d2' },
    linkBGHovColor: { type: 'string', default: '#4527a4' },
}

registerBlockType('services-section/service', {
    // Build In attributes
    title: __('Service', 'services-section'),
    description: __('Service item for services section', 'services-section'),
    parent: ['services-section/services'],
    icon: icons.services,
    category: 'widgets',
    keywords: [__('our service', 'services-section'), __('service item', 'services-section'), __('service card', 'services-section')],
    supports: { reusable: false, html: false },

    // Custom attributes
    attributes,

    // Build In Functions
    edit: Edit,

    save: () => null,

    example: {
        attributes: {
            'preview': true
        },
    },
});