import { __ } from '@wordpress/i18n';
import { useEffect } from '@wordpress/element';
import { RichText, InspectorControls } from '@wordpress/block-editor';
import { PanelBody, TextControl, ToggleControl, __experimentalUnitControl as UnitControl } from '@wordpress/components';

import servicePattern from '../img/service-pattern.png';

// Icon
import icons from '../Const/icons';

// Variables
import Title from '../../../Components/Title';
import BColor from '../../../Components/BColor';
import Background from '../../../Components/Background';
import IconControl from '../../../Components/IconControl';
import { ImageHolderControl } from '../../../Components/ImageControl';
import BorderControl from '../../../Components/BorderControl';
import options from '../Const/options';
const { pxUnit, perUnit, emUnit } = options;

const Edit = props => {
    const { className, attributes: { background, hoverBG, isIcon, isUpIcon, icon, upIcon, iconWidth, iconBorder, isTitle, title, titleColor, isDesc, desc, descColor, isLink, link, linkIn, linkIconColor, linkBGColor, linkBGHovColor }, setAttributes, clientId } = props;

    useEffect(() => { clientId && setAttributes({ cId: clientId }); }, [clientId]); // Set & Update clientId to cId

    // Get Patent Attributes
    // const parentBlock = select('core/block-editor').getBlockParents(clientId);
    // const parentAttributes = select('core/block-editor').getBlockAttributes(parentBlock);

    return <>
        <InspectorControls>
            <PanelBody className='bPlPanelBody' title={__('Service Settings', 'services-section')}>
                <Background label={__('Background:', 'services-section')} background={background} onChange={val => setAttributes({ background: val })} defaults={{ color: '#0000' }} />

                <Background label={__('Hover Background:', 'services-section')} background={hoverBG} onChange={val => setAttributes({ hoverBG: val })} defaults={{ type: 'image', image: { url: servicePattern }, overlayColor: '#0000', position: 'bottom left', size: 'auto' }} />
            </PanelBody>

            {isIcon && <PanelBody className='bPlPanelBody' title={__('Icon Settings', 'services-section')} initialOpen={false}>
                <ToggleControl label='Upload Icon?' checked={isUpIcon} onChange={val => setAttributes({ isUpIcon: val })} />

                {isUpIcon ? <>
                    {/* After Image */}
                    <ImageHolderControl className='mt20' label={__('Icon:', 'services-section')} img={upIcon} onChange={val => setAttributes({ upIcon: val })} />

                    <UnitControl className='mt20' label={__('Icon Width:', 'services-section')} labelPosition='left' value={iconWidth} onChange={val => setAttributes({ iconWidth: val })} units={[pxUnit, perUnit, emUnit]} />

                </> : <IconControl icon={icon} onChange={val => setAttributes({ icon: val })} defaults={{ class: 'fa fa-wordpress', fontSize: 70 }} />}

                {/* Border */}
                <BorderControl label={__('Border:', 'advanced-post-block')} border={iconBorder} onChange={val => setAttributes({ iconBorder: val })} />
            </PanelBody>}

            <PanelBody className='bPlPanelBody' title={__('Colors Settings', 'services-section')} initialOpen={false}>
                {isTitle && <BColor label={__('Title Color:', 'services-section')} value={titleColor} onChange={val => setAttributes({ titleColor: val })} defaultColor={'#222'} />}

                {isDesc && <BColor label={__('Description Color:', 'services-section')} value={descColor} onChange={val => setAttributes({ descColor: val })} defaultColor={'#333'} />}
            </PanelBody>

            {isLink && <PanelBody className='bPlPanelBody' title={__('Link Settings', 'services-section')} initialOpen={false}>
                <Title mt='0'>{__('Link:', 'services-section')}</Title>
                <TextControl value={link} onChange={val => setAttributes({ link: val })} />

                {'button' === linkIn && <>
                    <BColor label={__('Icon Color:', 'services-section')} value={linkIconColor} onChange={val => setAttributes({ linkIconColor: val })} defaultColor={'#fff'} />

                    <BColor label={__('Background Color:', 'services-section')} value={linkBGColor} onChange={val => setAttributes({ linkBGColor: val })} defaultColor={'#d2d2d2'} />

                    <BColor label={__('Background Hover Color:', 'services-section')} value={linkBGHovColor} onChange={val => setAttributes({ linkBGHovColor: val })} defaultColor={'#4527a4'} />
                </>}
            </PanelBody>}
        </InspectorControls>


        <div className={className} id={`ssbService-${clientId}`}>
            <style dangerouslySetInnerHTML={{
                __html: `
                #ssbService-${clientId} .ssbService{
                    ${background?.styles || `background-color: #0000;`}
                }
                #ssbService-${clientId} .ssbService .bgLayer{
                    ${hoverBG?.styles || `background-image: url(${servicePattern}); background-position: bottom left; background-size: auto; background-repeat: no-repeat; background-color: #0000;`}
                }

                #ssbService-${clientId} .ssbService .title{
                    color: ${titleColor};
                }
                #ssbService-${clientId} .ssbService .icon{
                    ${iconBorder?.styles || ''}
                }
                #ssbService-${clientId} .ssbService .icon i{
                    ${icon?.styles || 'font-size: 70px;'}
                }
                #ssbService-${clientId} .ssbService .icon img{
                    width: ${iconWidth};
                }
                #ssbService-${clientId} .ssbService .description{
                    color: ${descColor};
                }
                #ssbService-${clientId} .ssbService .link a{
                    background-color: ${linkBGColor};
                }
                #ssbService-${clientId}:hover .ssbService .link a{
                    background-color: ${linkBGHovColor};
                }
                #ssbService-${clientId} .ssbService .link a svg{
                    fill: ${linkIconColor};
                }
            `}} />

            <div className='ssbService'>
                {'service' === linkIn && <a className='serviceLink' href={link}></a>}
                <div className='bgLayer'></div>
                {isIcon && <div className='icon'>
                    {isUpIcon ? <img src={upIcon?.url} alt={upIcon?.alt} /> : icon?.class && <i className={icon?.class}></i>}
                </div>}

                {isTitle && <RichText className='title' tagName='h3' value={title} onChange={val => setAttributes({ title: val })} placeholder={__('Service Title', 'services-section')} />}

                {isDesc && <RichText className='description' tagName='p' value={desc} onChange={val => setAttributes({ desc: val })} placeholder={__('Service Description', 'services-section')} />}

                {isLink && 'button' === linkIn && <div className='link'><a href={link}>{icons.longArrowToRight}</a></div>}
            </div>
        </div>
    </>;
};
export default Edit;