import { registerBlockType } from '@wordpress/blocks';
import { useEffect } from '@wordpress/element';
import { InnerBlocks } from '@wordpress/block-editor';

// Import Files
import './child';
import './editor.scss';
import Settings from './settings';

// Icons
import icons from './Const/icons';
const icon = icons.services;

// Metadata
import metadata from '../block.json';
const { name, title, description, category, keywords, supports, attributes, example } = metadata;

registerBlockType(name, {
    title, description, icon, category, keywords, supports, attributes, example,

    // Build In Functions
    edit: props => {
        const { className, attributes: { layout, columns, columnGap, rowGap, background, textAlign, itemHeight, itemPadding, itemShadow, itemBorder, iconPadding, iconMargin, titleTypo, titleMargin, descTypo }, setAttributes, clientId } = props;

        useEffect(() => { clientId && setAttributes({ cId: clientId }); }, [clientId]); // Set & Update clientId to cId

        return <>
            <Settings attributes={props.attributes} setAttributes={setAttributes} clientId={clientId} />

            <div className={className} id={`ssbServices-${clientId}`}>
                <style dangerouslySetInnerHTML={{
                    __html: `
                    @import url(${titleTypo?.googleFontLink});
                    @import url(${descTypo?.googleFontLink});

                    #ssbServices-${clientId} .ssbServices{
                        ${background?.styles || 'background-color: #0000;'}
                    }
                    #ssbServices-${clientId} .ssbServices .block-editor-inner-blocks .block-editor-block-list__layout{
                        grid-gap: ${rowGap} ${columnGap};
                    }

                    #ssbServices-${clientId} .ssbServices .ssbService{
                        text-align: ${textAlign};
                        min-height: ${itemHeight};
                        padding: ${itemPadding?.styles || '50px 30px 50px 50px'};
                        box-shadow: ${itemShadow?.styles || '0 0 20px 0 #0000001a'};
                        ${itemBorder?.styles || 'border-radius: 15px;'}
                    }
                    #ssbServices-${clientId} .ssbServices .ssbService .bgLayer{
                        border-radius: ${itemBorder?.radius || '15px'}
                    }
                    #ssbServices-${clientId} .ssbServices .ssbService .icon{
                        padding: ${iconPadding?.styles || '0 0 10px 0'};
                        margin: ${iconMargin?.styles || '0 0 20px 0'};
                    }
                    #ssbServices-${clientId} .ssbServices .ssbService .title{
                        ${titleTypo?.styles || 'font-size: 23px;'}
                        margin: ${titleMargin?.styles || '0 0 30px 0'};
                    }
                    #ssbServices-${clientId} .ssbServices .ssbService .description{
                        ${descTypo?.styles || 'font-size: 16px;'}
                    }
                `}} />

                <div className={`ssbServices ${layout} columns-${columns.desktop} columns-tablet-${columns.tablet} columns-mobile-${columns.mobile}`}>
                    <InnerBlocks allowedBlocks={['services-section/service']} template={[['services-section/service'], ['services-section/service'], ['services-section/service']]} />
                </div>
            </div>
        </>
    },

    save: () => <InnerBlocks.Content />
});